.services {
  @include media1024 {
    padding-top: 100px;
  }

  &__title {
    @extend .poppins-600;
    font-size: 32px;
    line-height: 130%;

    text-align: left;
    color: #253757;

    margin-bottom: 24px;

    @include sub-title("Services");
  }

  &__text {
    margin-bottom: 48px;
  }

  &__paragraph {
    @extend .open-sans-400;
    font-size: 14px;

    color: $c-gray;

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__item {
    margin-bottom: 24px;
  }
  
  @include media640 {
    &__item:nth-child(2n) {
      transform: translateY(-50%);
    }
  }
  
  &__cards-wrap {
    @include media640 {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 30px;
    }
  }
}
